@tailwind base;
@tailwind components;
@tailwind utilities;

*{
  @apply text-white;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  @apply bg-neutral-900;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header_logo{
  width: 70px;
}

.nav li{
  cursor: pointer;
  position: relative;
}

.nav li::before{
  content: "";
  position: absolute;
  left: 0;
  bottom: -10px;
  height: 2px;
  width: 0;
  background-color: #fff;
  transition: all .5s ease;
}

.nav li:hover::before{
  width: 100%;
}

.search svg path{
  fill: black;
}

.meal-enter {
  opacity: 0;
}
.meal-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}
.meal-exit {
  opacity: 1;
}
.meal-exit-active {
  opacity: 0;
  transition: opacity 100ms ease-in;
}

.calculator-container {
  transition: width 0.5s ease;
}

.calculator-container-full {
  width: 100%;
}

.calculator-container-small {
  width: 70%;
}

.meal-list-enter {
  opacity: 0;
}

.meal-list-enter-active {
  opacity: 1;
  transition: opacity 500ms ease;
}

.meal-list-exit {
  opacity: 1;
}

.meal-list-exit-active {
  opacity: 0;
  transition: opacity 500ms ease;
}

@media(max-width: 1024px){
  .calculator-container-small{
     width: 100%;
  }

  .calculator-container button{
    margin-bottom: 20px;
  }
}

@media(max-width: 768px){
  .header_logo{
    width: 40px;
  }
  .activity-buttons{
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

figure {
  position: absolute;
  margin: auto;
  top: 0; bottom: 0; left: 0; right: 0;
  width: 6.250em; height: 6.250em;
  animation: rotate 2.4s linear infinite;
}
.white {
  top: 0; bottom: 0; left: 0; right: 0;
  background: white;
  animation: flash 2.4s linear infinite;
  opacity: 0;
}
.dot {
  position: absolute;
  margin: auto;
  width: 2.4em; height: 2.4em;
  border-radius: 100%;
  transition: all 1s ease;
}
.dot:nth-child(2) { top: 0; bottom: 0; left: 0; background: #FF4444; animation: dotsY 2.4s linear infinite; }
.dot:nth-child(3) { left: 0; right: 0; top: 0; background: #FFBB33; animation: dotsX 2.4s linear infinite; }
.dot:nth-child(4) { top: 0; bottom: 0; right: 0; background: #99CC00; animation: dotsY 2.4s linear infinite; }
.dot:nth-child(5) { left: 0; right: 0; bottom: 0; background: #33B5E5; animation: dotsX 2.4s linear infinite; }

@keyframes rotate {
  0% { transform: rotate( 0 ); }
  10% { width: 6.250em; height: 6.250em; }
  66% { width: 2.4em; height: 2.4em; }
  100%{ transform: rotate(360deg); width: 6.250em; height: 6.250em; }
}

@keyframes dotsY {
  66% { opacity: .1; width: 2.4em; }
  77%{ opacity: 1; width: 0; }
}
@keyframes dotsX {
  66% { opacity: .1; height: 2.4em;}
  77%{ opacity: 1; height: 0; }
}

@keyframes flash {
  33% { opacity: 0; border-radius: 0%; }
  55%{ opacity: .6; border-radius: 100%; }
  66%{ opacity: 0; }
}